@import "./var";

@mixin common-content-width {
  max-width: 1200px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include min-desktop-only {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }
  @include mobile-only {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}