@import './var.scss';
@import './theme';
@import "./mixins";

.sunCurve-wrap {
  .sunCurve-header-tvl {
    width: 90%;
    margin: 50px auto 0;
    max-width: 1200px;
  }
  .sunCurve-main {
    margin: 0 auto;
    @include common-content-width;
    .list-holder {
      min-height: 200px;
    }
  }
  .sunCurve-main-inner {
    border-radius: 20px;
    background: $color-table-bg;
    overflow: hidden;
  }
  .sunCurve-thead {
    display: flex;
    padding: 0 50px;
    font-size: 14px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    background: rgba(37, 31, 50, 0.3);
    color: rgba(255, 255, 255, 0.5);
    .sunCurve-thead-l,
    .sunCurve-thead-c {
      margin-right: 10px;
    }
    .sunCurve-thead-l,
    .sunCurve-thead-c,
    .sunCurve-thead-r {
      padding: 20px 0;
    }
    .sunCurve-thead-l {
      width: calc(max(50%, 500px));
    }
    .sunCurve-thead-c,
    .sunCurve-thead-r {
      flex: 1;
    }
  }
  .sunCurve-list {
    li {
      position: relative;
      display: flex;
      padding: 20px 40px;
      font-size: 15px;
      background: $color-table-bg;
      line-height: 30px !important;
      border-bottom: 1px solid rgba(#fff, 0.1);
      cursor: pointer;
      &:last-child {
        border-bottom: 0;
      }
      .sunCurve-value {
        font-size: 20px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #ffffff;
      }
      .sunCurve-label {
        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #ffffff;
        opacity: 0.7;
      }
    }
    .sunCurve-row-l,
    .sunCurve-row-c {
      margin-right: 10px;
    }
    .sunCurve-row-c,
    .sunCurve-row-r {
      .sunCurve-label {
        display: none;
      }
    }
    .sunCurve-row-l {
      width: calc(max(50%, 500px));
      .sunCurve-value {
        margin-right: 10px;
      }
    }
    .sunCurve-row-c {
      flex: 1;
    }
    .sunCurve-row-r {
      flex: 1;
    }
    .sunCurve-row {
      display: flex;
    }
    .sunCurve-row-l {
      .pool-icon {
        margin-right: 10px;
        height: 30px;
        img {
          display: block;
          height: 100%;
        }
      }
    }
    .icon-right {
      position: absolute;
      right: 30px;
      top: 50%;
      margin-top: -5px;
      width: 10px;
      height: 10px;
    }
  }
  .more-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 25px 0;
    line-height: 21px;
    font-size: 15px;
    font-family: PingFangSC-Semibold, PingFang SC;
    background: $color-table-bg;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    border-top: 1px solid rgba(#fff, 0.1);
    img {
      display: block;
      margin-left: 10px;
      width: 10px;
      height: 10px;
    }
  }
  .more-btn-active {
    img {
      transform: rotate(180deg);
    }
  }
  .sunCurve-loading {
    .ant-spin-container::after {
      background: transparent;
    }
  }
}

@include mobile-only {
  .sunCurve-wrap {
    .sunCurve-main {
      max-width: 100%;
    }
    .sunCurve-thead {
      display: none;
    }
    .sunCurve-main-inner {
      border-radius: 0px;
      background: transparent;
      overflow: visible;
    }
    .sunCurve-list {
      li {
        flex-wrap: wrap;
        padding: 10px;
        font-size: 12px;
        color: rgba(255, 255, 255, 1);
        margin-bottom: 12px;
        line-height: 17px !important;
        background: $color-table-bg;
        box-shadow: 0px 0px 4px 0px rgba(21, 21, 38, 0.5);
        border-radius: 5px;
        border: 0;
        .sunCurve-value {
          font-size: 12px;
          font-weight: 600;
          color: #ffffff;
        }
        .sunCurve-label {
          font-size: 12px;
          font-weight: 400;
          color: rgba(255, 255, 255, 0.5);
        }
      }
      .sunCurve-row-c,
      .sunCurve-row-r {
        .sunCurve-label {
          margin-right: 6px;
        }
      }
      .sunCurve-row-l {
        width: 100%;
        line-height: 20px;
        margin-right: 0;
        margin-bottom: 10px;
        .sunCurve-value {
          font-size: 12px;
          margin-right: 10px;
        }
      }
      .sunCurve-row-c,
      .sunCurve-row-r {
        .sunCurve-label {
          display: block;
        }
      }
      .sunCurve-row-c {
        flex: 1;
        margin-left: 0;
      }
      .sunCurve-row-r {
        flex: 1;
      }
      .sunCurve-row-l {
        .pool-icon {
          margin-right: 6px;
          height: 20px;
        }
      }
    }
    .more-btn {
      padding: 10px 0;
      line-height: 17px;
      font-size: 12px;
      border-radius: 5px;
      border: 0;
      background: $color-table-bg;
      box-shadow: 0px 0px 4px 0px rgba(21, 21, 38, 0.5);
      img {
        margin-left: 7px;
      }
    }
  }
}
